@use 'var';


body {
    font-family: var.$font-main;
    color: var.$color-white;
    margin: 0;

    position: relative;
    background: radial-gradient(circle at 50% 50%, #837276 0%, #6D5F62 29%, #574C4E 72%, #574C4E 100%);
}

body::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url(https://grainy-gradients.vercel.app/noise.svg);
    opacity: 30%;
    pointer-events: none;
}


p {
    margin: 0;
}

